}
a {
  outline: none;
  text-decoration: none;
}
html,
body {
  height: 100%;
}
body {
  font-family: 'Exo 2', sans-serif;
  font-size: 20px;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  min-height: 100%;
  width: 100%;
  padding: 4%;
  background: url("/media/images/back.jpg") left top repeat;
}
.wrapper:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  background: linear-gradient(to top, rgba(0,0,0,0.4) 0%, rgba(0,0,0,0) 50%, rgba(0,0,0,0.4) 90%);
}
.content {
  position: relative;
  width: 100%;
  z-index: 100;
}
.content h1 {
  color: #fff;
  text-align: center;
  font-size: 1.8em;
  font-weight: 300;
  margin: 0 0 1.5em;
}
.product-container {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.product-box {
  width: 16em;
  margin: 0 1em 1.5em;
}
.product-box.selected .product-box__container {
  border: 0.2em solid #d91667;
}
.product-box.selected .product-box__container:after {
  background: #d91667;
}
.product-box.selected .product-box__size {
  background: #d91667;
}
.product-box.disabled .product-box__container {
  cursor: default;
  border: 0.2em solid #b3b3b3;
}
.product-box.disabled .product-box__container:after {
  background: #b3b3b3;
}
.product-box.disabled .product-box__size {
  background: #b3b3b3;
}
.product-box.disabled .product-box__image,
.product-box.disabled .product-box__title,
.product-box.disabled .product-box__name,
.product-box.disabled .product-box__composition,
.product-box.disabled .product-box__count,
.product-box.disabled .product-box__gift {
  opacity: 0.5;
  color: #b3b3b3;
}
.product-box.disabled .product-box__footnote {
  color: #ff6;
}
.product-box__container {
  cursor: pointer;
  background: #fff;
  margin-bottom: 0.75em;
  height: 24em;
  padding: 1.25em 1.25em 0.5em 2.5em;
  border: 0.2em solid #1698d9;
  clip-path: polygon(16% 0%, 100% 0%, 100% 100%, 0 100%, 0 12%);
  border-radius: 1em;
  overflow: hidden;
  position: relative;
  transition: border 0.3s;
}
.product-box__container:after {
  content: ' ';
  width: 4em;
  height: 0.3em;
  background: #1698d9;
  position: absolute;
  top: 1.2em;
  left: -1em;
  transform: rotate(-48deg);
  transition: background 0.3s;
}
.product-box__image {
  position: absolute;
  bottom: 0;
  left: 0;
  z-index: -1;
}
.product-box__image--pos {
  left: -1.3em;
  bottom: -4.5em;
}
.product-box__title {
  font-family: 'TrebuchetMS', sans-serif;
  font-size: 0.8em;
  color: #666;
  margin-bottom: 0.75em;
}
.product-box__name {
  font-family: 'TrebuchetMS-Bold', sans-serif;
  font-size: 2.4em;
  line-height: normal;
  font-weight: bold;
  color: #000;
}
.product-box__composition {
  font-family: 'TrebuchetMS-Bold', sans-serif;
  font-size: 1.2em;
  line-height: normal;
  font-weight: bold;
  color: #000;
  margin-bottom: 0.5em;
}
.product-box__count,
.product-box__gift {
  font-family: 'TrebuchetMS', sans-serif;
  font-size: 0.6em;
  color: #666;
}
.product-box__count-num,
.product-box__gift-num {
  font-family: 'TrebuchetMS-Bold', sans-serif;
}
.product-box__size {
  font-family: 'TrebuchetMS', sans-serif;
  font-size: 1.05em;
  color: #fff;
  border-radius: 50%;
  background: #1698d9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 4em;
  height: 4em;
  position: absolute;
  bottom: 0.75em;
  right: 0.75em;
  overflow: hidden;
  transition: background 0.3s;
}
.product-box__size-num {
  font-size: 2.1em;
  line-height: 1em;
}
.product-box__footnote {
  font-family: 'TrebuchetMS', sans-serif;
  font-size: 0.65em;
  color: #fff;
  text-align: center;
}
.product-box__footnote-button {
  cursor: pointer;
  font-family: 'TrebuchetMS-Bold', sans-serif;
  font-size: 1.05em;
  color: #1698d9;
  text-decoration: underline;
  text-decoration-style: dashed;
}
.product-box__footnote-button:hover {
  text-decoration: none;
}
